<template>
  <div class="detail" v-if="empty">
    <div class="content">
      <div class="img"><img :src="list.drugImg"></div>
      <div class="prod">
        <h3 class="name">{{list.drugName}}<span>（ 0.5x10片x2板/盒）</span></h3>
        <p class="price">${{list.minprice}}.00 ~ ${{list.maxprice}}.00<span>（共{{list.pizhunwenhao}}个报价）</span></p>
        <p class="standard">批准文号：国药准字<span>H20013{{list.pizhunwenhao}}</span></p>
        <p class="prodCompany">生成企业：{{list.qiye}}</p>
      </div>
    </div>
    <p class="Effect">作用：{{list.zuoyong}}</p>
    <router-link :to='`/detail/${key+1}`'
      v-for='(item, key) in list.yaofanglist'
      :key='key'
    >
      <div class="business">
        <div class="listBusliness">
            <p class="Pharmacy">{{item.list.yaofangName}}</p>
            <span class="posi">{{item.list.province}} {{item.list.dijishi}}</span>
            <span>评分：<i>{{item.list.pingfeng.toFixed(1)}}分</i></span>
            <p class="fahuo">24小时发货</p>
        </div>
        <div class="qian">
          <p class="HowMoney">${{item.list.price}}</p>
          <p class="yunfei">运费 {{item.list.yunfei}} 元</p>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import { getBusinesslist } from '@/service.js'
export default {
  name: 'detail',
  data () {
    return {
      msg: 'qwe',
      list: {},
      empty: false
    }
  },
  mounted () {
    getBusinesslist().then(res => {
      if (res.data.code === '200') {
        this.list = res.data.data
        this.empty = true
      }
    })
  }
}
</script>

<style lang='scss' scoped>
  .detail {
    background-color: #dedede;
    height: 100%;
    .content {
      background: #fff;
      display: flex;
      .img {
        width: 30%;
        text-align: center;
        padding-top: 20%;
        position: relative;
        img{
          max-width: 100%;
          max-height: 100%;
          padding-top: 10px;
          position: absolute;
          top: 5px;
          left: 5px;
        }
      }
      .prod {
        width: 100%;
        padding-top: 3%;
        padding-left: 10%;
        h3 {
          font-size: 16px;
          span {
            font-size: 14px;
            font-weight: normal;
          }
        }
        .price {
          color: red;
          font-weight: 600;
          padding: 20px 0;
          span {
            font-weight: normal;
            font-size: 14px;
            color:#997;
          }
        }
        .standard, .prodCompany {
          padding-bottom: 10px;
          font-size: 14px;
          color:#997;
        }
      }
    }
    .Effect {
      background: #fff;
      color:#997;
      font-size: 14px;
      padding-left: 10px;
      padding-bottom: 10px;
      border-bottom: 1px solid #999;
      margin-bottom: 10px;
    }
    .business{
      background: #fff;
      border-top: 1px solid #999;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      line-height: 30px;
      .listBusliness {
        .Pharmacy {
          font-size: 16px;
          color: #000;
        }
        span {
          color: #999;
          i {
            font-style: normal;
            color:rgb(15, 179, 10);
          }
        }
        .fahuo {
          color:rgb(15, 179, 10);
          font-size: 14px;
        }
      }
      .qian {
        .HowMoney {
          color: rgba(219, 25, 25, 0.911);
          font-weight: 600;
        }
        .yunfei {
          font-size: 12px;
          color:rgb(70, 67, 67);
        }
      }
    }
  }
</style>



